  background: -webkit-linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url('https://images.unsplash.com/photo-1445140463371-d8036feedc2f?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=733ea6bce7a4ec3571b9a8ba39387bb7&auto=format&fit=crop&w=1052&q=80');
  background: -o-linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url('https://images.unsplash.com/photo-1445140463371-d8036feedc2f?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=733ea6bce7a4ec3571b9a8ba39387bb7&auto=format&fit=crop&w=1052&q=80');
