.centered-landing {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, 600%);
      -ms-transform: translate(-50%, 600%);
          transform: translate(-50%, 600%);
}
.centered-landing-btn {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, 800%);
      -ms-transform: translate(-50%, 800%);
          transform: translate(-50%, 800%);
  background: #f1f0eb !important;
}
.text-shadow {
  text-shadow: 0px 2px 2px rgba(2, 2, 2, 0.7)
}



